import styled from 'styled-components';

const InputStyled = styled.input` 
             *{
               box-sizing: border-box;
              } 
                border: 0;
                clip: rect(0 0 0 0);
                height: 1px;
                margin: -1px;
                overflow: hidden;
                padding: 0;
                position: absolute;
                width: 1px;
                      	
         `;

export {InputStyled};



